$logo-big: 110px;
$logo: 40px;
$big-padding: 100px;
$collapse: 700px;
$sticky: true;

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  box-shadow: var(--shadow);

  @if $sticky {
    position: sticky !important;
    top: 0;
    z-index: 10 !important;
  }
}

header a {
  color: var(--text);
  text-decoration: none;
}

.home {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

.logo {
  height: $logo;
}

.logo > * {
  width: unset;
  height: 100%;
}

.logo img,
.logo svg {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.title-text {
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
  flex-wrap: wrap;
  gap: 5px;
  min-width: 0;
  font-family: var(--title);
  text-align: left;
}

.title {
  font-size: var(--large);
}

.subtitle {
  opacity: 0.65;
  font-weight: var(--thin);
}

.nav-toggle {
  display: none;
  position: relative;
  width: 30px;
  height: 30px;
  margin: 0;
  color: var(--text);
  appearance: none;
  transition: background var(--transition);
}

.nav-toggle:after {
  content: "\f0c9";
  position: absolute;
  left: 50%;
  top: 50%;
  color: var(--text);
  font-size: 15px;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  transform: translate(-50%, -50%);
}

.nav-toggle:checked:after {
  content: "\f00d";
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  font-family: var(--heading);
  text-transform: uppercase;
  // Slightly larger nav text site-wide for readability.
  font-size: 1.1rem;
}

nav > a {
  padding: 7px 8px;
}

nav > a:hover {
  color: var(--primary);
}

header:not([data-big]) {
  @media (max-width: $collapse) {
    justify-content: flex-end;

    .nav-toggle {
      display: flex;
    }

    .nav-toggle:not(:checked) + nav {
      display: none;
    }

    nav {
      align-items: flex-end;
      flex-direction: column;
      width: 100%;
    }
  }
}

header[data-big] {
  // Responsive homepage logo width (logo.png is rectangular).
  // Tune this to control how large the DBI Lab logo appears.
  --home-logo-w: clamp(220px, 20vw, 320px);
  // PI profile width target (keeps the right block consistent).
  --home-profile-w: 260px;

  // Desktop layout: logo on the left, banner text in one row, PI profile on the right.
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: auto auto;
  grid-template-areas:
    "home profile"
    "nav  profile";
  column-gap: 40px;
  row-gap: 18px;
  justify-content: center;
  align-items: center;
  padding: $big-padding 20px;

  @if $sticky {
    top: unset;
  }

  // Reclaim horizontal space so the title can stay in one row.
  .home-profile-spacer {
    display: none;
  }

  .home {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    // More space between logo and banner title.
    gap: clamp(28px, 4vw, 80px);
    flex-grow: 0;
    grid-area: home;
    justify-self: start;
  }

  .logo {
    width: var(--home-logo-w);
    height: auto;
    flex-shrink: 0;
  }

  // Override the global logo sizing rules for homepage so the PNG keeps
  // its natural aspect ratio while scaling up.
  .logo > * {
    width: 100%;
    height: auto;
  }

  .logo-spacer {
    display: none;
  }

  .title-text {
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    min-width: 0;
  }

  nav {
    // Make homepage navigation more prominent, but still responsive.
    grid-area: nav;
    justify-self: center;
    font-size: clamp(1.05rem, 1.6vw, 1.25rem);
    gap: 14px;
  }

  nav > a {
    padding: 8px 10px;
  }

  .title {
    // Home banner title should be visibly larger and responsive.
    font-size: clamp(2.0rem, 3.6vw, 2.9rem);
    white-space: nowrap;
  }

  .subtitle {
    // Home banner subtitle should scale with viewport, but remain secondary.
    font-size: clamp(1.2rem, 2.2vw, 1.8rem);
  }

  .home-profile {
    grid-area: profile;
    justify-self: end;
    align-self: center;
    max-width: var(--home-profile-w);
  }

  // Tighten the portrait component spacing inside the header.
  .home-profile .portrait {
    margin: 0;
    width: 260px;
    max-width: 100%;
  }

  .home-profile .portrait-image {
    margin-bottom: 14px;
  }

  @media (max-width: 1100px) {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;
    grid-template-areas:
      "home"
      "profile"
      "nav";
    column-gap: 0;
    row-gap: 20px;

    .home-profile {
      justify-self: center;
      max-width: 320px;
    }

    .home-profile-spacer {
      display: none;
    }
  }
}
